
.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width:100%;
    height:600px;
    background:#ffffff;
    background-image: url("https://i.ibb.co/vcHxPx7/Untitled-design-5.png");
    background-size:80%;
    background-position-x: 60%;
    background-position-y: 10%;
    background-repeat: no-repeat;
    object-fit: cover;
    border: 1px solid black;
}

.jamieImg{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.showcaseImg{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 10px;
    margin-left: 3vw;
    width:500px;
    border: 2px solid black;
    box-shadow: 0 0 10px 0;
}

.showcase{
    display: flex;
    width:500px;
    align-items: center;
    padding-bottom: 200px;
    flex-direction: column;
}

.showcaseTitle{
    display: flex;
    font-size: 50px;
    font-weight: 700;
    font-family: 'Trebuchet MS', serif;
    text-shadow: 2px 2px green;
    padding-top: 300px;
    margin-bottom: 0px;
}


.showcaseText{
    display: flex;
    flex-direction: center;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-family: Trebuchet MS;
    font-weight: normal;
    color:rgb(60, 61, 60);
    width:500px;
    padding: 20px;
    padding-bottom: 30px;
}

.link{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    bottom: 50px;
    margin-left: .3vw;
    text-decoration:none;
    font-family: Verdana;
    font-weight: bold;
    margin-bottom: 50px;
    color: black;
    padding: 10px;
    border: 2px solid black;
    box-shadow: 2px 2px 2px 2px;
}


/* .nameBox{
    background:#CDEEAA;
}

.jobBox{
    background:#FFFFD2
} */

.showcaseBox{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.jamiePP{
    width:200px;
    border-radius: 50%;
    box-shadow: 0 4px 4px 4px grey;
}

.nameTitle{
    margin-top: 2vw;
    font-family: Georgia;
    letter-spacing: .3vw;
    font-weight: bold;
    font-size: 3.7vw;
}


.jobTitle{
    font-family: Verdana;
    font-weight: 300;
    color:black;
    font-size:1.3vw;
    margin-bottom: 100px;
}

.header{
    display:none;
    position:fixed;
    width:100%;
    height:70px;
    background:#6D70C6;
    z-index: 100;
}

.web1{
    background:linear-gradient(to right,#96DAE4, white);
    width:100%;
    height:600px;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.web2{
    background: linear-gradient(to left,#CDEEAA, white);
    width:100%;
    height:600px;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.web3{
    width:100%;
    height:600px;
    background:#FFFFD2;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.footer{
    background: lightgrey;
    width:100%;
    height:600px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;
}

.footerText{
    font-size: 80px !important;
    margin-top: 300px !important;
    padding: 20px !important;
}

.topRight{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}


